// Tipos do Jogo Arquivo Morto

export type GameMode = 'numbers' | 'cards' | 'words';

export interface GameState {
  // Modo atual
  mode: GameMode;
  
  // Estado do modo Nmeros
  numSelected: number | null;
  numHidden: boolean;
  numRevealed: boolean;
  
  // Estado do modo Cartas
  cardSrc: string;
  cardSpinning: boolean;
  
  // Estado do modo Palavras
  wordsSelected: string[];
  wordsRevealed: boolean;
  
  // Metadados
  lastUpdate: number;
}

export const DEFAULT_GAME_STATE: GameState = {
  mode: 'numbers',
  numSelected: null,
  numHidden: false,
  numRevealed: false,
  cardSrc: '/assets/verso.png',
  cardSpinning: false,
  wordsSelected: [],
  wordsRevealed: false,
  lastUpdate: Date.now()
};

